.home-slider
    .swiper-container
        position: relative
        .swiper-wrapper
            position: relative
            .swiper-slide 
                .item 
                    width: auto 
                    height: calc(100vh - 130px)
                    background: #333333
                    img 
                        height: 100%
                        width: 100%
                        object-fit: cover
                    // &:before 
                    //     content: ''
                    //     position: absolute 
                    //     width: 100%
                    //     height: 100px 
                    //     bottom: 0 
                    //     left: 0
                    .text-slider
                        padding: 15px
                        width: 100%
                        position: absolute
                        bottom: 60px
                        left: 0
                        text-align: center
                        p 
                            font-size: 40px
                            text-align: center
                            color: #ffff
                            
                @media (max-width: 768px)
                    .item
                        img
                            
                        .text-slider 
                            p 
                                font-size: 30px
        .home-swiper-pagination
            position: absolute
            bottom: 0
            left: 0
            z-index: 99
            padding: 30px
            display: flex
            justify-content: center
            span
                width: 10px
                height: 10px
                border: none
                opacity: 1
                border-radius: 50px
                margin: 0 5px
                background: #ffffff
                position: static
                &.swiper-pagination-bullet-active
                    background: #00a3e6

            

                
