.gioithieu
    padding: 60px 0
    .box-left
        h1 
            font-size: 36px
            color: #00a3e6
            @media (max-width: 1025px)
                font-size: 26px
            
        h4 
            font-size: 22px
            color: #999999
            padding-bottom: 20px
        p 
            font-size: 16px
            color: #333333
            margin-bottom: 40px 0
            text-align: justify
        .btn
            width: 160px
            height: 40px
            margin-top: 2rem
            border: 1px solid #00a3e6
            color: #00a3e6
            text-transform: uppercase
            display: flex
            justify-content: center
            text-align: center
            align-items: center
            
            em 
                margin-left: 10px
            &:hover 
                background: #00a3e6
                transition: .3s all ease-in-out
                color: #ebebeb
    @media (max-width: 1025px)
        .box-left
            margin-bottom: 20px
            button 
            
    .box-right
        img 
            width: 100%
            height: 100%
            object-fit: cover
@media (max-width: 768px)
    .gioithieu
        padding: 40px 0
